


















{ Query } = require "~plugins/memory-record"

module.exports =
  mixins: [
    require("~plugins/browser-store")
      replace:
        tag_id: "all"
  ]
  computed:
    name_blanks: ->
      Query.faces.name_blank()

    name_counts: ->
      Query.faces.name_head(@tag_id)




